<template>
  <section id="main">
    <Dialog
      :title="title"
      @close="onClose"
      @cancel="onCancel"
      @ok="onConfirm"
      v-show="showDialog"
    >
      <template>
        <iframe class="dialogiframe" src="http://kong.midea.com/" />
      </template>
    </Dialog>
    <div id="viewer" v-if="!showPhotoView"></div>
    <div id="viewerList" v-else>
      <div
        :id="'viewer' + item.id"
        v-for="(item, index) of photoViewList"
        class="viewerItem"
        :key="index"
      />
    </div>
  </section>
</template>

<script>
/**
 * removeMarker   markerId
 */
import Dialog from "./dialog.vue";
import { Viewer } from "photo-sphere-viewer";
import "photo-sphere-viewer/dist/photo-sphere-viewer.css";

import { MarkersPlugin } from "photo-sphere-viewer/dist/plugins/markers";
import "photo-sphere-viewer/dist/plugins/markers.css";

import markerData from "./markerData";
export default {
  name: "photoSphere",
  data() {
    return {
      step1: require("../assets/heyou/护士站2.jpg"),
      step2: require("../assets/heyou/护士站3.jpg"),
      step3: require("../assets/heyou/护士站.jpg"),
      step4: require("../assets/heyou/过道2.jpg"),
      step5: require("../assets/heyou/普通病房.jpg"),
      step6: require("../assets/heyou/VIP病房3.jpg"),
      step7: require("../assets/heyou/普通病房.jpg"),
      stepVIew: null,
      viewer: null,
      showDialog: false, //  marker点击处理对象
      showPhotoView: false, //  marker点击处理对象
      photoViewList: [],
      photoViewObj: new Map(),
      photoViewMarkersP: new Map(),
      title: "",
    };
  },
  components: {
    Dialog,
  },
  watch: {
    $route(val) {
      if (val.name == "photoSphere") {
        console.log(val);
        this.init();
      }
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    onDialog(content) {
      // 调用Dialog弹出对话框
      this.content = "Content of the modal ...";
      this.showDialog = true;
    },
    onClose() {
      // 关闭dialog
      this.showDialog = false;
    },
    onCancel() {
      // “取消”按钮回调
      this.showDialog = false;
    },
    onConfirm() {
      // “确定”按钮回调
      this.showDialog = false;
    },
    init() {
      if (this.photoViewList) {
        this.photoViewList.forEach((element) => {
          try {
            const viewer = document.querySelector("#viewer" + element.id);
            if (viewer) {
              viewer.removeChild(viewer.childNodes[0]);
            }
          } catch (error) {
            
          }
        });
      }
      if(this.photoViewMarkersP){
        for(let key of this.photoViewMarkersP.keys()){
          let item = this.photoViewMarkersP.get(key)
          try {
            item.clearMarkers()
          } catch (error) {
            console.error(error)
          }
        }
      }
      this.showDialog = false; //  marker点击处理对象
      this.showPhotoView = false; //  marker点击处理对象
      this.photoViewList = [];
      this.photoViewObj = new Map();
      this.photoViewMarkersP = new Map();
      try {
        this.markersPlugin.clearMarkers();
      } catch (error) {}
      if (!this.$route.params.id) {
        this.stepVIew = this.step1;
        this.initPhotoSphere();
        return false;
      }
      switch (Number(this.$route.params.id)) {
        case 1:
          this.stepVIew = this.step1;
          break;
        case 2:
          this.stepVIew = this.step2;
          break;
        case 3:
          this.stepVIew = this.step3;
          break;
        case 4:
          this.stepVIew = this.step4;
          break;
        case 5:
          this.stepVIew = this.step5;
          break;
        case 6:
          this.stepVIew = this.step6;
          break;
        case 7:
          this.stepVIew = this.step7;
          break;
        default:
          this.stepVIew = this.step1;
          break;
      }
      if (!this.viewer) {
        this.initPhotoSphere();
      } else {
        this.changeImage(this.stepVIew);
      }
    },
    markerClick(marker) {
      this.$eventBus.$emit("refreshCursor");
      if (marker.data.type == "photoView") {
        const viewer = document.querySelector("#viewer");
        if (viewer) {
          viewer.removeChild(viewer.childNodes[0]);
          this.viewer = null
        }
        this.showPhotoView = true;
        this.photoViewList = marker.data.link;
        this.$nextTick(() => {
          this.initMarkerPhoto();
        });
      }
      if (marker.data.type == "dialog") {
        this.title = marker.data.name;
        this.showDialog = true;
      }
    },
    initMarkerPhoto() {
      let that = this;
      // this.viewer.destroy()
      that.photoViewList.forEach((element) => {
        let stepVIew = require("../assets/heyou/" + element.url);
        let viewer = new Viewer({
          container: document.querySelector("#viewer" + element.id),
          panorama: stepVIew,
          mousewheel: false,
          fisheye: 1,
          size: {
            width: "100%",
            height: "100%",
          },
          plugins: [
            [
              MarkersPlugin,
              {
                markers: [],
              },
            ],
          ],
          navbar: ["autorotate", "zoom", "markers", "caption", "fullscreen"],
        });
        let markersPlugin = viewer.getPlugin(MarkersPlugin);
        markersPlugin.on("select-marker", (e, marker) => {
          that.markerClick(marker);
        });

        that.photoViewObj.set(element.id, viewer);
        that.photoViewMarkersP.set(element.id, markersPlugin);
        viewer.on('click',(e,data)=>{
          console.log(data)
        })
        viewer.once("ready", () => {
          that.initTheMarker(element.id);
        });
      });
    },
    initPhotoSphere() {
      console.log("initPhotoSphere");
      if (!this.stepVIew) {
        return false;
      }
      const that = this;
      this.$nextTick(() => {
        that.viewer = new Viewer({
          container: document.querySelector("#viewer"),
          panorama: that.stepVIew,
          mousewheel: false,
          fisheye: 1,
          size: {
            width: "100%",
            height: "100%",
          },
          plugins: [
            [
              MarkersPlugin,
              {
                markers: [],
              },
            ],
          ],
          navbar: ["autorotate", "zoom", "markers", "caption", "fullscreen"],
        });
        that.markersPlugin = that.viewer.getPlugin(MarkersPlugin);
        that.markersPlugin.on("select-marker", (e, marker) => {
          that.markerClick(marker);
        });
        that.viewer.once("ready", () => {
          that.initTheMarker();
        });
      });
    },
    initTheMarker(id = null) {
      console.log('initTheMarker:'+id)
      try {
        this.markersPlugin.clearMarkers();
      } catch (error) {}
      try {
        if(this.photoViewMarkersP && this.photoViewMarkersP.get(id)){
          let item = this.photoViewMarkersP.get(id);
          item.clearMarkers()
        }
      } catch (error) {}
      const list = id ? markerData.markerData[id] : markerData.markerData[this.$route.params.id];
      if(!list){
        return false
      }
      for (let key = 0; key < list.length; key++) {
        const element = list[key];
        this.setMarker(element, key, id);
        if(id && key == 0){
          let view = this.photoViewObj.get(id)
          view.animate({
            longitude: element.point.longitude,
            latitude: element.point.latitude,
            speed: '-2rpm',
          })
        }
      }
      this.$eventBus.$emit("refreshHover");
    },
    setMarker(data, index = 0,markersPluginID = null) {
      if (!data && !data.point) {
        return false;
      }
      // 添加初始标注 具体标注编辑后会更新
      // currentTabIndex 1: 添加标注 2:添加跳转
      // console.log('添加标注')
      const id = `marker${+new Date()}` + index;
      const config = {
        id,
        data,
        html: '<div class="hover-target"><svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="patterns"><circle class="loading-circle-1" cx="50" cy="25" r="10" stroke="#12507bc4" fill="#224e7f" /></svg></div>',
        svgStyle: {},
        longitude: data.point.longitude,
        latitude: data.point.latitude,
        anchor: "center center",
        tooltip: {
          // tooltip with custom position
          content:
            `
              <div class="marker-tooltip-container">
                <p class="marker-title">
                  ` +
            data.name +
            `
                </p>
              </div>
            `,
          position: "center top",
        },
        visible: true, // 标注初始显示与否
        type: "marker",
        status: 1,
        isInitialize: true,
        attachment: {
          type: "img",
          imgArr: [],
          videoArr: [],
        },
      };
      if(!markersPluginID){
        this.markersPlugin.addMarker(config);
      }else{
        let markersPlugin = this.photoViewMarkersP.get(markersPluginID);
        console.log(markersPlugin)
        markersPlugin.addMarker(config);
      }
      
      // 显示 tooltip
      // this.markersPlugin.markers[id].showTooltip();
    },
    changeImage(obj) {
      let that = this;
      this.viewer.setPanorama(obj, {}, true).then(() => {
        that.initTheMarker();
      });
    },
  },
};
</script>

<style scope>
#main {
  width: 100%;
  height: 100%;
  position: relative;
}
#viewer {
  width: 100%;
  height: 100%;
}
#viewerList {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.viewerItem {
  width: 100%;
  height: 100%;
}
.dialogiframe {
  width: 100%;
  height: 100%;
  border: none;
  padding: 0;
  margin: 0;
}
.patterns {
  width: 96px;
  height: 48px;
}
@keyframes morph {
  0% {
    stroke-width: 10;
    stroke-opacity: 0.1;
  }
  25% {
    stroke-width: 30;
    stroke-opacity: 0.3;
  }
  50% {
    stroke-width: 10;
    stroke-opacity: 0.5;
  }
  75% {
    stroke-width: 10;
    stroke-opacity: 0.3;
  }
  100% {
    stroke-width: 10;
    stroke-opacity: 0;
  }
}
.loading-circle-1 {
  -webkit-animation: morph 2s ease infinite;
  animation: morph 2s ease infinite;
}
</style>