<template>
  <div id="app">
    <section id="main">
      <header class="cd-header">
        <div class="header-wrapper">
          <div class="logo-wrap">
            <a href="#" class="hover-target"><span>和佑</span>国际医院</a>
          </div>
          <div class="nav-but-wrap">
            <div class="menu-icon hover-target">
              <span class="menu-icon__line menu-icon__line-left"></span>
              <span class="menu-icon__line"></span>
              <span class="menu-icon__line menu-icon__line-right"></span>
            </div>
          </div>
        </div>
      </header>

      <div class="nav">
        <div class="nav__content">
          <ul class="nav__list">
            <li
              class="nav__list-item"
              :class="{ 'active-nav': active == 0 }"
              @click="showStep(0)"
            >
              <a href="#" class="hover-target">首页</a>
            </li>
            <li
              class="nav__list-item"
              :class="{ 'active-nav': active == 1 }"
              @click="showStep(1)"
            >
              <a href="#" class="hover-target">护士站</a>
            </li>
            <li
              class="nav__list-item"
              :class="{ 'active-nav': active == 2 }"
              @click="showStep(2)"
            >
              <a href="#" class="hover-target">VIP病房</a>
            </li>
            <li
              class="nav__list-item"
              :class="{ 'active-nav': active == 3 }"
              @click="showStep(3)"
            >
              <a href="#" class="hover-target">普通病房</a>
            </li>
            <li
              class="nav__list-item"
              :class="{ 'active-nav': active == 4 }"
              @click="showStep(4)"
            >
              <a href="#" class="hover-target">过道</a>
            </li>
          </ul>
        </div>
      </div>

      <div class="section full-height over-hide">
        <div class="switch-wrap">
          <keep-alive>
            <router-view />
          </keep-alive>
        </div>
      </div>

      <div class="cursor" id="cursor"></div>
      <div class="cursor2" id="cursor2"></div>
      <div class="cursor3" id="cursor3"></div>
    </section>
  </div>
</template>

<script>
import photoSphere from "./components/photoSphere.vue";
import svgmain from "./components/bimMain.vue";
export default {
  name: "App",
  data() {
    return {
      active: 0,
    };
  },
  components: {
    photoSphere,
    svgmain,
  },
  mounted() {
    this.initMouse()
    this.initPage();
    this.setTheHoverCss();
    let that = this;
    this.$eventBus.$on("refreshHover", () => {
      that.setTheHoverCss();
    });
    this.$eventBus.$on("refreshCursor", () => {
      that.initcursor();
    });
  },
  watch: {
    $route() {
      this.initcursor();
    },
  },
  methods: {
    initMouse() {
      function throttle(fn, delay = 100) {
        let timer = null
        return function () {
            if(timer) return
            timer = setTimeout(() => {
              fn.apply(this,arguments)
              timer = null
            })
        }
      }
      function move(n) {
        (t.style.left = n.clientX + "px"),
          (t.style.top = n.clientY + "px"),
          (e.style.left = n.clientX + "px"),
          (e.style.top = n.clientY + "px"),
          (i.style.left = n.clientX + "px"),
          (i.style.top = n.clientY + "px");
      }
      document
        .getElementsByTagName("body")[0]
        .removeEventListener("mousemove", throttle(move));
      document
        .getElementsByTagName("body")[0]
        .addEventListener("mousemove", throttle(move));
      var t = document.getElementById("cursor"),
        e = document.getElementById("cursor2"),
        i = document.getElementById("cursor3");
    },
    initcursor() {
      var t = document.getElementById("cursor"),
        e = document.getElementById("cursor2"),
        i = document.getElementById("cursor3");
      e.classList.remove("hover"), i.classList.remove("hover");
    },
    setTheHoverCss() {
      console.log('setTheHoverCss')
      var t = document.getElementById("cursor"),
        e = document.getElementById("cursor2"),
        i = document.getElementById("cursor3");
      function n(t) {
        e.classList.add("hover"), i.classList.add("hover");
      }
      function s(t) {
        e.classList.remove("hover"), i.classList.remove("hover");
      }
      s();
      for (
        var r = document.querySelectorAll(".hover-target"), a = r.length - 1;
        a >= 0;
        a--
      ) {
        o(r[a]);
      }
      function o(t) {
        t.removeEventListener("mouseover", n),
          t.removeEventListener("mouseout", s);
        t.addEventListener("mouseover", n), t.addEventListener("mouseout", s);
      }
    },
    initPage() {
      (function ($) {
        "use strict";
        //Navigation

        var app = (function () {
          var body = undefined;
          var menu = undefined;
          var menuItems = undefined;
          var init = function init() {
            body = document.querySelector("body");
            menu = document.querySelector(".menu-icon");
            menuItems = document.querySelectorAll(".nav__list-item");
            applyListeners();
          };
          var applyListeners = function applyListeners() {
            menu.addEventListener("click", function () {
              return toggleClass(body, "nav-active");
            });
          };
          var toggleClass = function toggleClass(element, stringClass) {
            if (element.classList.contains(stringClass))
              element.classList.remove(stringClass);
            else element.classList.add(stringClass);
          };
          init();
        })();

        //Switch light/dark

        $("#switch").on("click", function () {
          if ($("body").hasClass("light")) {
            $("body").removeClass("light");
            $("#switch").removeClass("switched");
          } else {
            $("body").addClass("light");
            $("#switch").addClass("switched");
          }
        });
      })(jQuery);
    },
    showStep(step) {
      this.active = step;
      switch (step) {
        case 0:
          document.querySelector(".menu-icon").click();
          this.$router.push(`/`);
          break;
        case 1:
          document.querySelector(".menu-icon").click();
          this.$router.push(`/photoSphere/1`);
          break;
        case 2:
          document.querySelector(".menu-icon").click();
          this.$router.push(`/photoSphere/5`);
          break;
        case 3:
          document.querySelector(".menu-icon").click();
          this.$router.push(`/photoSphere/7`);
          break;
        case 4:
          document.querySelector(".menu-icon").click();
          this.$router.push(`/photoSphere/4`);
          break;
        default:
          document.querySelector(".menu-icon").click();
          this.$router.push(`/`);
          break;
      }
    },
  },
};
</script>

<style scoped src="./components/base.css"></style>
