<template>
  <section class="main">
    <!-- <div id="hidden" @click="customView">获取</div> -->
    <section id="bim"></section>
  </section>
</template>

<script>
let app;
var bimFace_viewer = null;
var bimFace_wt = null;
var model3D = null;
var bimFace_drawableConfig = null;
var bimFace_drawableContainer = null;
import PointerImage from "../assets/point.png";
export default {
  name: "bimMain",
  data() {
    return {
      fileUrl: "",
      PointerImage,
      tags: [
        {
          x: 9641805.678042006,
          y: -4200413.990266836,
          z: -0,
        },
        {
          x: 9649372.2933332,
          y: -4192507.9672156367,
          z: -0,
        },
        {
          x: 9649515.437618464,
          y: -4189259.1679078317,
          z: -0,
        },
        {
          x: 9646790.548008606,
          y: -4201220.034246437,
          z: -0,
        },
        {
          x: 9650403.255946813,
          y: -4205060.407342247,
          z: -0,
        },
        {
          x: 9660204.344495164,
          y: -4208040.7229184965,
          z: -0,
        },
      ],
      homeView: {"name":"persp","position":{"x":9633363.722729774,"y":-4197000.897667989,"z":30737.27778207609},"target":{"x":9651963.067295482,"y":-4201713.290733921,"z":-5269.136436623805},"up":{"x":0.8554885024188777,"y":-0.21674956346459728,"z":0.4702755032606021},"fov":45,"zoom":1,"version":1,"coordinateSystem":"world"}
    };
  },
  mounted() {
    this.fileUrl =
      "http://ioc.smartbuilding.top:9300/990919efbc60b4904d385b81469f4cfd/";
    this.initBim(this.fileUrl);
  },
  methods: {
    customView(){
      let homeview = bimFace_viewer.getCameraStatus();
      // bimFace_viewer.recordCustomHomeview(homeview);
      console.log(JSON.stringify(homeview));
      bimFace_viewer.render();
    },
    toHide() {
      // 对关注构件进行着色
      bimFace_viewer.deactivateComponentsById(["10000721764379", "0"]);
      bimFace_viewer.render();
    },
    bimReLoad() {
      if (document.getElementById("bim")) {
        document.getElementById("bim").innerHTML = "";
      }
      if (bimFace_viewer) {
        try {
          bimFace_viewer.destroy();
        } catch (error) {
          console.error(error);
        }
      }
      bimFace_viewer = null;
      bimFace_wt = null;
    },
    initBim(fileUrl) {
      if (fileUrl) {
        var BimfaceLoaderConfig = new BimfaceSDKLoaderConfig();
        BimfaceLoaderConfig.dataEnvType = BimfaceEnvOption.Local;
        BimfaceLoaderConfig.sdkPath = `${fileUrl}/jssdk`; // 对应模型——jssdk
        BimfaceLoaderConfig.path = `${fileUrl}/viewToken.json`; // 对应模型——viewToken.json
        BimfaceSDKLoader.load(
          BimfaceLoaderConfig,
          this.onSDKLoadSucceeded,
          this.onSDKLoadFailed
        );
      }
    },
    onSDKLoadSucceeded(viewMetaData) {
      if (viewMetaData.viewType == "3DView") {
        let that = this;
        var view = document.getElementById("bim");
        var config = new Glodon.Bimface.Application.WebApplication3DConfig();
        config.domElement = view;
        var eventManager = Glodon.Bimface.Application.WebApplication3DEvent;
        var viewapp = new Glodon.Bimface.Application.WebApplication3D(config);
        bimFace_viewer = viewapp.getViewer();
        bimFace_viewer.addModel(viewMetaData);
        bimFace_viewer.toggleContextMenuDisplay(false)
        var config = new Glodon.Bimface.Plugins.Walkthrough.WalkthroughConfig();
        config.viewer = bimFace_viewer;
        bimFace_wt = new Glodon.Bimface.Plugins.Walkthrough.Walkthrough(config);

        bimFace_viewer.addEventListener(
          Glodon.Bimface.Viewer.Viewer3DEvent.ViewAdded,
          () => {
            that.toHide()
            bimFace_drawableConfig =
              new Glodon.Bimface.Plugins.Drawable.DrawableContainerConfig();
            bimFace_drawableConfig.viewer = bimFace_viewer;
            bimFace_drawableContainer =
              new Glodon.Bimface.Plugins.Drawable.DrawableContainer(
                bimFace_drawableConfig
              );
            that.initCustom()
            bimFace_viewer.setCameraStatus(that.homeView);
            bimFace_viewer.render();
            that.$eventBus.$emit('refreshHover')
          }
        );
      }
    },
    onSDKLoadFailed(error) {
      console.log("Failed to load SDK!");
    },
    initCustom(){
      for (let index = 0; index < this.tags.length; index++) {
        const element = this.tags[index];
        this.addCusTom(element,index)
      }
    },
    addCusTom(worldPosition,index){
      let _this = this
      let config = new Glodon.Bimface.Plugins.Drawable.CustomItemConfig();
      config.content = '<div class="hoverCustomItem hover-target"> <img src="' + PointerImage + '" class=""/></div>';
      config.viewer = bimFace_viewer;
      config.worldPosition = worldPosition;
      let customItem = new Glodon.Bimface.Plugins.Drawable.CustomItem(config);
      customItem.onClick(function (item) {
        _this.Analyevents(index);
      });
      bimFace_drawableContainer.addItem(customItem);
    },
    Analyevents(d) {
      this.$router.push(`/photoSphere/`+(d+1));
    },
  },
};
</script>

<style scope>
#bim {
  width: 100%;
  height: 100%;
}
.bim {
  display: none;
}
.main {
  position: relative;
  width: 100%;
  height: 100%;
}
#hidden {
  position: fixed;
  z-index: 999;
  bottom: 0;
  left: 0;
  width: 50px;
  background: #000;
  color: #fff;
}
.bf-house,
.bf-toolbar {
  display: none;
}
.hoverCustomItem{
  cursor: pointer;
}
.hoverCustomItem img{
  width: 15px;
  height: 15px;
}
